import React from "react";
import { P } from "..";
import style from "./Description.module.scss";

const Description: React.FC = () => {
  return (
    <section className={style.description}>
      <P size="px22" className={style.description__title}>
         <b></b>        
          <span> !</span>   
         ,        
         <span>  .</span>
      </P>
    </section>
  );
};

export default Description;
